import React, { Component } from "react";
import {useState} from 'react';
 
function Help(props) {

    const [email, setEmail] = useState('');
   

    const emailChange = event => {
        setEmail(event.target.value);
      };

    return(props.trigger) ? (
        <div className = "popup">
            <div className= "popup-inner">
                <h1>We are incredibly sorry you've encountered an issue.</h1>
                <p>Please let us know how we can help!</p>
                <form>
                <div className = 'inputContainer'>
                    <label for="cars">General Issue:</label>
                    <select name="issues" id="issues">
                        <option value="volvo">Players</option>
                        <option value="saab">Teams</option>
                        <option value="opel">Registration</option>
                        <option value="audi">Other</option>
                    </select>
                </div>
                <div className = 'inputContainer'>
                        <label>Your Email
                            <input
                            id ='email'type="text" 
                            style={{
                                marginLeft:20,
                                }}
                            placeholder="An email to contact you.."
                            onChange={emailChange}
                            value={email}
                            />
                        </label>
                    </div>
                        
                  
                    <br/>
                    <div className = 'inputContainer'>
                        <label>Issue:
                            <input
                            type="text" 
                            placeholder="Please describe the issue you are having.."
                            style={{
                                width:450,
                                marginLeft:50,
                                }}
                            />
                        </label>
                    </div>
                            
                        
                         
                 </form>
                 <div>
                    <button onClick={props.closeHelp}className="backBtn">Close</button>
                    <button onClick={() =>props.submitHelp()} className='submitBtn'>Submit</button>  
                 </div>
                 
            </div>
        </div>
    ) :  "";
}

export default Help