import React from "react";
import { useState } from "react"
import axios from 'axios';
import { useTable, useSortBy } from 'react-table';
import { Icon } from 'semantic-ui-react'

const PlayerTable = (props) => {
    const data = props.data


    const columns = React.useMemo(
        () => [
          {
            Header: 'Player Name',
            accessor: 'Name', // accessor is the "key" in the data
          },
          {
            Header: 'Position',
            accessor: 'Position',
          },
          {
            Header: 'Games Played',
            accessor: 'Games', // accessor is the "key" in the data
          },
          {
            Header: 'Goals',
            accessor: 'Goals', // accessor is the "key" in the data
          },
          {
            Header: 'Assists',
            accessor: 'Assists', // accessor is the "key" in the data
          },
          {
            Header: 'Points',
            accessor: 'Points', // accessor is the "key" in the data
          },
        ],
        []
    )

    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        rows,
        prepareRow,
      } = useTable({ columns, data}, useSortBy);

    return (
        <div>
   <table className="statTable"{...getTableProps()} style={{ border: 'solid 1px black' }}>
     <thead>
     {headerGroups.map(headerGroup => (
         <tr {...headerGroup.getHeaderGroupProps()}>
           {headerGroup.headers.map(column => (
               <th
                   {...column.getHeaderProps(column.getSortByToggleProps())}
                   style={{
                    background: 'lightgray',
                    color: 'black',
                    fontWeight: 'bolder',
                  }}
               >
                 {column.render('Header')}
                 <span>
                   {column.isSorted
                       ? column.isSortedDesc
                           ? <Icon name='sort up' /> 
                           :  <Icon name='sort down'/>
                       : <Icon disabled name='sort' />}
                </span>
               </th>
           ))}
         </tr>
     ))}
     </thead>
     <tbody {...getTableBodyProps()}>
     {rows.map(row => {
       prepareRow(row)
       return (
           <tr {...row.getRowProps()}>
             {row.cells.map(cell => {
               return (
                   <td
                       {...cell.getCellProps()}
                       style={{
                         padding: '1px',
                         border: 'solid 1px gray',
                         backgroundColor: 'white',
                       }}
                   >
                     {cell.render('Cell')}
                   </td>
               )
             })}
           </tr>
       )
     })}
     </tbody>
   </table>
 </div>



);
}

   
export default PlayerTable;