import React from "react";
class TeamBanner extends React.Component {
   
    // Constructor 
    constructor(props) {
        super(props);
      
        this.state = {
            items: [],
            DataisLoaded: false
        };
    }
    // ComponentDidMount is used to
    // execute the code 
    componentDidMount ()  {
        fetch(
            "https://statsapi.web.nhl.com/api/v1/teams/"+this.props.num+"/stats")
            .then((res) => res.json())
            .then((json) => {
                this.setState({
                    items: json,
                    DataisLoaded: true
                });
            })
        
            
    }
    render() {
        const { DataisLoaded, items } = this.state;
        if (!DataisLoaded) return <div>
            <h1> Please wait some time.... </h1> </div> ;
   
        return (
        <div className = "App">
            <h1 className = "titleText" style={{backgroundColor: "white"}}>{items.stats[0].splits[0].team.name} ({items.stats[0].splits[0].stat.wins} , {items.stats[0].splits[0].stat.losses} , {items.stats[0].splits[0].stat.ot})</h1>
        
           

            
        </div>
    );
}
}
   
export default TeamBanner;