import React from "react";
class TeamStats extends React.Component {
   
    // Constructor 
    constructor(props) {
        super(props);
      
        this.state = {
            items: [],
            DataisLoaded: false
        };
    }
    // ComponentDidMount is used to
    // execute the code 
    componentDidMount ()  {
        fetch(
            "https://statsapi.web.nhl.com/api/v1/teams/"+this.props.num+"/stats")
            .then((res) => res.json())
            .then((json) => {
                this.setState({
                    items: json,
                    DataisLoaded: true
                });
            })
        
            
    }
    render() {
        const { DataisLoaded, items } = this.state;
        if (!DataisLoaded) return <div>
            <h1> Please wait some time.... </h1> </div> ;
   
        return (
        <div className = "App">
            <tbody className ="rosterTable">
                    <tr>
                        <th>Statistic</th>
                        <th>Value</th>
                        <th>Ranking</th>
                        
                    </tr>
                    <tr>
                        <td>Wins</td>
                        <td>{items.stats[0].splits[0].stat.wins}</td>
                        <td>{items.stats[1].splits[0].stat.wins}</td>
                    </tr>
                    <tr>
                        <td>Losses</td>
                        <td>{items.stats[0].splits[0].stat.losses}</td>
                        <td>{items.stats[1].splits[0].stat.losses}</td>
                    </tr>
                    <tr>
                        <td>Points</td>
                        <td>{items.stats[0].splits[0].stat.pts}</td>
                        <td>{items.stats[1].splits[0].stat.pts}</td>
                    </tr>
                    <tr>
                        <td>Powerplay %</td>
                        <td>{items.stats[0].splits[0].stat.powerPlayPercentage}%</td>
                        <td>{items.stats[1].splits[0].stat.powerPlayPercentage}</td>
                    </tr>
                    <tr>
                        <td>Penalty Kill %</td>
                        <td>{items.stats[0].splits[0].stat.penaltyKillPercentage}%</td>
                        <td>{items.stats[1].splits[0].stat.penaltyKillPercentage}</td>
                    </tr>
                    <tr>
                        <td>Shots For / Game</td>
                        <td>{items.stats[0].splits[0].stat.shotsPerGame}</td>
                        <td>{items.stats[1].splits[0].stat.shotsPerGame}</td>
                    </tr>
                    <tr>
                        <td>Shots Against/Game</td>
                        <td>{items.stats[0].splits[0].stat.shotsAllowed}</td>
                        <td>{items.stats[1].splits[0].stat.shotsAllowed}</td>
                    </tr>
                    <tr>
                        <td>Faceoff Win %</td>
                        <td>{items.stats[0].splits[0].stat.faceOffWinPercentage}%</td>
                        <td>{items.stats[1].splits[0].stat.faceOffWinPercentage}</td>
                    </tr>
                    <tr>
                        <td>Team Save %</td>
                        <td>{items.stats[0].splits[0].stat.savePctg}</td>
                        <td>{items.stats[1].splits[0].stat.savePctRank}</td>
                    </tr>
            </tbody>
            
        </div>
    );
}
}
   
export default TeamStats;