import React from "react";
import { useState } from "react"
import axios from 'axios';
import { useTable, useSortBy } from 'react-table';
import PlayerTable from "./PlayerTable";

const PlayerList = (props) => {
   
    const num = props.num;
    const [players,setPlayers] = useState([])
    const [playersStats, setPlayersStats] = useState([])
    const [playerList, setPlayerList] = useState([])
    const [complete, setComplete] = useState(false)
   

        const fetchStats = () => {
            if(!complete){ 

                const teamAPI  = "https://statsapi.web.nhl.com/api/v1/teams/"+num+"/roster"
            const getPlayers = axios.get(teamAPI);
            axios.all([getPlayers]).then(
                axios.spread((...allData) =>{
                    const allDataPlayers = allData[0]
                    var players = allDataPlayers.data.roster
                    setPlayers(players)
                    var playerStatArray = []
                    for(var i = 0 ; i < players.length ; i++){
                        const playerStatsAPI = "https://statsapi.web.nhl.com/api/v1/people/"+players[i].person.id+"/stats?stats=statsSingleSeason&season=20212022"
                        const getPlayerStats = axios.get(playerStatsAPI);
                        axios.all([getPlayerStats]).then(
                            axios.spread((...allData) =>{
                                var allDataPlayer = allData[0]
                                playerStatArray.push(allDataPlayer)
                                
                                
                                
                                
                        }))
          

                        
                    }
                    setPlayersStats(playerStatArray)
                    mergeArrays()                  
                }
                )
            )
            }
            
        }

        var mergeArrays = () => {
            
            if (players.length === playersStats.length && playersStats.length > 0){
                var combination = []
                for(var i = 0; i < players.length ; i++){
                    var playerName = players[i].person.fullName
                    var position = players[i].position.abbreviation
                    var games ='-'
                    var goals ='-'
                    var assists = '-'
                    var points = '-'
                    if(playersStats[i].data.stats[0].splits[0] !== undefined){
                        if(playersStats[i].data.stats[0].splits[0].stat.games !== undefined){
                            games = playersStats[i].data.stats[0].splits[0].stat.games
                        }
                        if(playersStats[i].data.stats[0].splits[0].stat.goals){
                            goals = playersStats[i].data.stats[0].splits[0].stat.goals
                        }
                        if(playersStats[i].data.stats[0].splits[0].stat.assists){
                            assists = playersStats[i].data.stats[0].splits[0].stat.assists 
                        }
                        if(playersStats[i].data.stats[0].splits[0].stat.points ){
                            points = playersStats[i].data.stats[0].splits[0].stat.points
                        }
                                      
                        
                        
                    }
                    var json ={ Name:""+playerName,
                                Position:""+position,
                                Games:""+games,
                                Goals:""+goals,
                                Assists:""+assists,
                                Points:""+points}
                    combination.push(json)
               
                    
                                
                }
     
                setPlayerList(combination)
                setComplete(true)
                
               
            }
           
            
        }
       
   
        return (
            
            <div>
                <PlayerTable  data= {playerList}/>
                <button hidden='true'onClick={fetchStats()}>Expand</button>
               
        
                
            </div>
           
            
        )
}

   
export default PlayerList;