import logo from './images/Logo.png'; // with import
import {Link} from 'react-router-dom';
import React from "react";
import home from './images/home.png';
import team from './images/team.png';
import signup from './images/signup.png';
import login from './images/LogIn.png';
import help from './images/help.png';

class Navbar extends React.Component {
    
    constructor(props) {
       
        super(props);
    }
 
    render (){
        this.state = { signUpName: this.props.signUpName,
                       logInName: this.props.logInName 
        };
        return (  
        
           <nav className ="navbar">
                <Link  to="/" className="title">Top Shelf Stats</Link>
                <img className="logo" alt="Logo" src={logo}/>
                <div className="links">
                    <Link to="/" className=""><img className='icon'alt="home" src={home}/>Home</Link>
                    <Link to="/teams" className=""><img className='icon'alt="team" src={team}/>Teams</Link>
                    <Link  onClick={this.props.openSignUp}><img className='icon2'alt="signup" src={signup}/>{this.state.signUpName}</Link>  
                    <Link  onClick={this.props.openLogIn } className=""><img className='icon'alt="login" src={login}/>{this.state.logInName}</Link>
                    <Link  onClick={this.props.openHelp} className=""><img className='icon'alt="help" src={help}/>Help</Link>
                </div>
                
                
            </nav>
            
            
        );
    }
   
}
 
export default Navbar;