import React, { Component } from "react";
import {useState} from 'react';
 
function LogIn(props) {

    const [username, setUsername] = useState('');

    const usernameChange = event => {
        setUsername(event.target.value);
      };

    return(props.trigger && props.name =='Log In') ? (
        <div className = "popup">
            <div className= "popup-inner">
                <h1>Please enter your log-in information:</h1>
                <form>
                <div className = 'inputContainer'>
                        <label>Username:
                            <input
                            style={{
                                marginLeft:10
                                }}
                            id ='username'type="text" 
                            placeholder="Your username.."
                            onChange={usernameChange}
                            value={username}
                            />
                        </label>
                    </div>
                        
                  
                    <br/>
                    <div className = 'inputContainer'>
                        <label>Password:
                            <input
                            style={{
                                marginLeft:10
                                }}
                            type="password" 
                            placeholder="Your password.."
                            
                            />
                        </label>
                    </div>
                            
                        
                         
                 </form>
                 <div>
                    <button onClick={props.closeLogIn}className="backBtn">Close</button>
                    <button onClick={() =>props.submitLogIn({username})} className='submitBtn'>Submit</button>  
                 </div>
                 
            </div>
        </div>
    ) : (props.trigger) ?  (
        <div className = "popup">
        <div className= "popup-inner">
            <div>
            <h1>See you soon!</h1>
            <p>You have succesfully been logged out.</p>
            <button onClick={() =>props.logOut()} className='submitBtn2'>Okay</button>  
            </div>
             
        </div>
        </div>
    ): "";
}

export default LogIn