import React, { Component } from "react";
import {useState} from 'react';
 
function Feedback(props) {
    return(props.trigger)?(
    
        <div className = "popup">
        <div className= "popup-inner">
            <div>
            <h1>We appreciate the feedback!!</h1>
            <p>We will contact you once we have a solution.</p>
            <button onClick={() =>props.submitFeedback()} className='submitBtn2'>Okay</button>  
            </div>
             
        </div>
        </div>
    
        ) : "";
}
export default Feedback
