import React, { Component } from "react";
import {useState} from 'react';
 
function SignUp(props) {

    const [username, setUsername] = useState('');

    const usernameChange = event => {
        setUsername(event.target.value);
      };

    return(props.trigger && props.name == 'Sign Up') ? (
        <div className = "popup">
            <div className= "popup-inner">
                <h1>We require some information to sign-up:</h1>
                <form>
                    <div className = 'inputContainer'>
                        <label>Username:
                            <input
                            style={{
                                marginLeft:85
                                }}
                            type="text" 
                            placeholder="Enter a username.."
                            onChange={usernameChange}
                            value={username}
                            />
                        </label>
                    </div>
                    <br/>
                    <div className = 'inputContainer'>
                        <label>Email Address:
                            <input
                            style={{
                                marginLeft:60
                                }}
                            type="text" 
                            placeholder="Enter an email address.."
                            />
                        </label>
                    </div>
                    <br/>
                    <div className = 'inputContainer'>
                        <label>Password:
                            <input
                            style={{
                                marginLeft:90
                                }}
                            type="password"
                            placeholder="Enter a password.."
                            />
                        </label>
                    </div>
                    <br/>
                    <div className = 'inputContainer'>
                        <label>Re-enter the Password:   
                            <input
                            
                            type="password" 
                            placeholder="Re-enter password.."
                            />
                        </label>
                    </div>     
                 </form>
                <button onClick={props.closeSignUp}className="backBtn">Close</button>
                <button onClick={() =>props.submitSignUp({username})}className='submitBtn'>Submit</button>   
            </div>
        </div>
    ) : "";
}

export default SignUp