import React from 'react'
import { ImageGroup, Progress } from 'semantic-ui-react'
import { useState } from "react"
import iceRink from './images/iceRink.png'
import {Link} from 'react-router-dom';
import rules from './images/rules.png';
import positions from './images/positions.png'
import topPlayers from './images/greatestPlayers.png'



function Learn() {

    const [module, setModule] = useState(1);
    const [progress, setProgress] = useState(0);
    const [image, setImage] = useState(iceRink);
    const [backBtnDisable, setbackBtnDisable] = useState(true);
    const [nextBtnDisable, setNextBtnDisable] = useState(false);
    const [backBtnColor, setbackBtnColor] = useState('lightgray');
    const [nextBtnColor, setNextBtnColor] = useState('');

    const updateTitle =() => {
        switch(module){
            case 1:
                return 'What is Ice Hockey?'
            case 2:
                return 'Ice Hockey Rules 101'
            case 3:
                return 'Hockey Positions'
            case 4:
                return 'Top 10 Greatest Players'
        }
    }

    const updateImage = () => {
        switch(module){
            case 1:
                return iceRink
            case 2:
                return rules
            case 3:
                return positions
            
            case 4:
                return topPlayers
        }
    }
    const updateText = () =>{
        switch(module){
            case 1:
                return <text>The objective of hockey is simple: score more goals than the opposing team.
                Players are not allowed to kick the puck into the net or purposely direct it in with any
                part of their body. During regulation time, each team uses five skaters—three forwards and 
                two defencemen—plus a goaltender. There are four officials in NHL games—two referees and two linesmen. They stop play for penalties and for things like offside or icing. When a player violates one of the rules of the game, he is given a penalty by a referee.
                Penalties are given for body fouls such as hitting from behind, elbowing and fighting. Penalties are also given for stick fouls like slashing, spearing, hooking, holding, tripping, cross-checking and high-sticking. Penalties can also be given for other infractions such as delay of game, too many men on the ice, and unsportsmanlike conduct.</text>
            
            case 2: 
                return <text>National Hockey League games are contested over three 20-minute periods. If the score is tied after 60 minutes, the game moves to a five-minute, sudden-death overtime where the first goal wins.

                If the game remains tied after overtime, a shootout is held until a winner is determined. The centre-ice line divides the rink in half. The bluelines delineate each team’s defensive zone. The area between the bluelines is called the neutral zone.

At the far ends of the rink, a red line extends the width of the ice. That is the goal line. If the puck legally crosses the goal line into the net, a goal is scored.

The goal crease—coloured blue—is the goaltender’s domain. The area directly behind the net marked by red lines is called the trapezoid. The goaltender is allowed to play the puck within this area but is penalized if he plays the puck below the goal line outside the trapezoid.
                
                </text>
            

            case 3:
                return <text>
                    There are six positions in hockey: three forwards—comprised of a centre and two wingers—two defencemen, plus one goaltender. The centre is flanked on the right and left by wingers, who generally play along the boards on their respective sides of the ice. They are primarily relied upon as goal scorers. The goaltender’s job is to prevent the puck from going in the net. They remain in their crease the entire game and wear different equipment than forwards or defencemen in order to better protect them from pucks.
                </text>
            
            case 4:
                return <text>
                    10. Eddie Shore was a mean defenceman and one of the league’s first big stars.<br/>

9. Bobby Hull terrorized goalies with his deadly slapshot.<br/>

8. Jean Béliveau played at a high level, with the dignified presence of a great statesman.<br/>

7. Doug Harvey was a smooth skater who did it all on defence.<br/>

6. Many call Mark Messier the greatest leader in hockey history.<br/>

5. Maurice “The Rocket” Richard was the first player to score 50 goals in a season and remains a Francophone icon.<br/>

4. Mario Lemieux dazzled with quite possibly the most natural ability of anyone who ever played.<br/>

3. Gordie Howe could score, he could hit and he kept doing it all into his 50s; that’s why they call him “Mr. Hockey.”<br/>

2. Bobby Orr, “Number 4,” changed the way the game is played with his attacking style from his defence position.<br/>

1. Wayne Gretzky scored more goals and points than anybody in NHL history. In fact, “The Great One” has more assists than anyone else has points.<br/><br/>
                </text>
        }
    }
    const increment = () =>{
        if(module < 4){
            setModule(module + 1)
            setProgress(progress+33)
            setbackBtnDisable(false)
            setbackBtnColor('')
        }
        if(module === 3){
            setNextBtnDisable(true)
            setNextBtnColor('lightgray')
        }
    }
    const decrement = () =>{
        if(module > 1){
            setModule(module - 1)
            setProgress(progress-33)
            setNextBtnDisable(false)
            setNextBtnColor('')
            
            
        }
        if(module === 2){
            setbackBtnDisable(true)
            setbackBtnColor('lightgray')
        }
    }

  return (
    <div>
        <br/>
      <h1 className="learnTitle">Learn about Hockey!</h1>
      <div className="progressBar">
      <a target="_blank" className="creds" href="https://www.sportsnet.ca/hockey/hockey-101-rules-of-the-game/#:~:text=The%20objective%20of%20hockey%20is,two%20defencemen%E2%80%94plus%20a%20goaltender">Courtesy of Sportsnet</a>
        <Progress percent={progress} active color='green'>
            Hockey IQ Progress
        </Progress>
      </div>
      
        <div className="module">
            <div className="moduleTitle">
                <h1>{updateTitle()}</h1>
            </div>
            <br/>
            <div className="moduleText">
                <p>{updateText()}</p>
            </div>
            <div className="moduleImageDiv">
                <img className='moduleImage'alt="team" src={updateImage()}/>
            </div>
            <div className="btns">
            <button disabled={backBtnDisable} style={{backgroundColor: backBtnColor}}className="backBtn2" onClick={decrement}>Back</button>
            <button disabled={nextBtnDisable}className="nextBtn" style={{backgroundColor: nextBtnColor}} onClick={increment}>Next</button>
            </div>
        </div>
        
        
        
    </div >
  );
}

export default Learn;