import Navbar from './Navbar';
import Home from './Home';
import News from './News';
import TwitterFeed from './TwitterFeed';
import Teams from './Teams';
import Team from './Team';
import SignUp from './SignUp';
import {useState} from 'react';
import LogIn from './LogIn';
import Help from './Help';
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';
import Feedback from './Feedback';
import Learn from './Learn';




function App() {

  const [signUpPopUp, setSignUpPopUp] = useState(false);
  const [logInPopUp, setLogInPopUp] = useState(false);
  const [helpPopUp, setHelpPopUp] = useState(false);
  const [feedBackPopUp, setfeedBackPopUp] = useState(false);

  const [logInName, setLogInName] = useState('Log In');
  const [signUpName, setSignUpName] = useState('Sign Up');

  function openHelp(){
    setHelpPopUp(true);
  }
  function closeHelp(){
    setHelpPopUp(false);
  }
  function submitHelp(){
    setHelpPopUp(false);
    setfeedBackPopUp(true);
  }
  function openSignUp(){
    setSignUpPopUp(true);

  }
  function closeSignUp(){
    setSignUpPopUp(false);

  }
  function submitFeedback(){
    setfeedBackPopUp(false);
  }

  function submitSignUp(name){
    setSignUpName('Welcome, '+name.username);
    setLogInName('Log Out');
    setSignUpPopUp(false);
  }

  function logOut(){
    setSignUpName('Sign Up');
    setLogInName('Log In');
    setLogInPopUp(false);
  }

  function openLogIn(){
    setLogInPopUp(true);

  }
  function closeLogIn(){
    setLogInPopUp(false);

  }

  function submitLogIn(name){
    setSignUpName('Welcome, '+name.username);
    setLogInName('Log Out');
    setLogInPopUp(false);
  }
  return (
    <Router>
      <div className="App">
        <SignUp submitSignUp={submitSignUp}closeSignUp ={closeSignUp} trigger={signUpPopUp} name={signUpName}>
                </SignUp>
        <LogIn submitLogIn={submitLogIn}closeLogIn ={closeLogIn} logOut={logOut} trigger={logInPopUp} name={logInName}>
                </LogIn>
        <Help trigger={helpPopUp} closeHelp={closeHelp} submitHelp={submitHelp}>
                </Help>
        <Feedback trigger={feedBackPopUp} submitFeedback={submitFeedback}>
                </Feedback>
        <Navbar openLogIn={openLogIn} logInName={logInName} signUpName={signUpName} openSignUp = {openSignUp} openHelp= {openHelp}/>
        <div className="content">
          <Switch>
            <Route path="/teams">
            
                <Teams />
            </Route>
            <Route path="/learn">
            
                <Learn />
            </Route>
            <Route path="/team">
                <Team />
            </Route>
            <Route path="/">
              <Home/>
              <div className="wrapper">
                <News/>
                <TwitterFeed/>
              </div>
            </Route>

            
            
          </Switch>
          
          
        </div>
      </div>
    
    </Router>
    
  );
}

export default App;
